//ProdDescInfoView
var ProdDescInfoView = React.createClass({

	getInitialState: function(){
		return {
			prodDesc: ""
		}
	},

	componentWillMount: function(){
		window.addEventListener("scroll", this.detectScroll);
	},

	componentDidMount: function(){
		$(function(){
			var $tabTarget = $(".prodDescTabs > .slider");
			var $tabContentClass = $tabTarget.next("ul").attr("class");

			$tabTarget.children("div").addClass("tab");
			$tabTarget.next("ul").removeClass("").attr("class","tabsContent "+$tabContentClass);
			$tabTarget.find("div.tab").eq(0).addClass("tab-current");

			$tabTarget.find("div.tab").each(function(){
				$(this).find("a").click(function(){
					var $index = $(this).parent("div").index();
					$(this).parent("div").addClass("tab-current").siblings("div.tab-current").removeClass("tab-current");
					$("ul.tabsContent").children("li").eq($index).addClass("show").siblings("li.show").removeClass("show");
				});
			});

			$tabTarget.slick({
				infinite: false,
				slidesToShow: 3,
				slidesToScroll: 3,
				dots: false,
				arrows: false
			});

			$(".prodDescTabs .btn-prev").on("click", function(){ $(".prodDescTabs > .slider").slick("slickPrev"); });
			$(".prodDescTabs .btn-next").on("click", function(){ $(".prodDescTabs > .slider").slick("slickNext"); });
		});

	},

	detectScroll: function(event){
		var scrollT = (window.pageYOffset !== undefined) ? window.pageYOffset : (document.documentElement || document.body.parentNode || document.body).scrollTop;
		var target = document.getElementById("prodDesc"),
			taParent = document.getElementById("content");
		var targetPos = getElementOffset(target).top,
			taParentPos = getElementOffset(taParent).top,
			taTotalPos = targetPos + taParentPos;

		if(scrollT > 55) {
			this.setState({prodDesc:this.props.prodDesc });
		}

		function getElementOffset(element){
		    var de = document.documentElement;
		    var box = element.getBoundingClientRect();
		    var top = box.top + window.pageYOffset - de.clientTop;
		    var left = box.left + window.pageXOffset - de.clientLeft;
		    return { top: top, left: left };
		}

	},

	render: function(){
		return(
			<div id="prodDesc" className="prodDesc">
				<div className="tabs prodDescTabs">
					<button type="button" className="btn-arrow btn-prev">Previous</button>
					<button type="button" className="btn-arrow btn-next">Next</button>
					<div className="slider multiple-items">
						<div><a href="javascript:void(0);"><span>商品說明</span></a></div>
						<div><a href="javascript:void(0);"><span>運送/購買說明</span></a></div>
						<div><a href="javascript:void(0);"><span>網友留言</span></a></div>
					</div>
					<ul className="prodDescTabContent">
						<li className="show" dangerouslySetInnerHTML={{ __html: this.state.prodDesc }}></li>
						<li>
							<ul>
	                            <li>送貨範圍：我們提供的商品配送區域僅限於台灣本島。</li>
	                            <li>到貨天數：商品預計訂單成立(即確認交易條件無誤、付款完成且仍有庫存)後 7 個工作天內送達(不含週六日及國定假日)。如廠商有約定日將於約定日期內送達，約定日期需於訂單成立後 14天內。</li>
	                            <li>換貨說明：若您收到的新品有瑕疵、破損、規格不符或是零件短缺的狀況，請您於收到貨後7天內， 至「我的帳戶&gt;我的訂單」頁面點選「我要換貨」，填寫需換貨的原因/事由，我們將會回覆您辦理換貨事宜。<br/>
	                                若需更換新品，換貨廠商將於收到您退回的商品後更換給您。(換貨商品必須為全新狀態且完整包裝，包括且不限於主機、附件、內外包裝、原廠紙箱、隨機文件或資料之完整性等)</li>
	                            <li>退貨說明：根據《消費者保護法》，於新蛋全球生活網購物的會員，可享有商品貨到次日起7天猶豫期的權益。 提醒您猶豫期並非試用期，您所退回的商品必須是全新狀態(不得有刮傷、破損、受潮)，並且保持完整包裝， 意即必須恢復至您收到商品時的原始狀態(包括且不限於商品本體、配件、原廠包裝、保護袋、保麗龍、保證書、隨附文件、贈品等)。 否則將影響您的退貨權益，也可能依照損毀程度扣除回復原狀之必要費用。<span class="blue UnderLine"><a href="/Policies/Return" target="_blank">(各類商品退貨規定說明)</a></span></li>
	                            <li>待商品收回並驗收無誤後，我們將進行退款，並於退款日當天發送E-mail通知您。</li>
	                            <li>商品保固：依原廠/出貨廠商標示為主。</li>
	                            <li>本網站全館免運費。</li>
	                        </ul>
							<p>其它更多有關台灣新蛋生活網說明請至<span class="blue">首頁&gt;服務說明&gt;<a href="/Service/AboutService" target="_blank">售後服務</a></span></p>
      					</li>
						<li>3</li>
					</ul>
				</div>
			</div>
		);
	}
});

module.exports = ProdDescInfoView;



