//prodPhotoImg Slider
var ProdPhotoView = React.createClass({

	getInitialState: function(){
		return {
			prodPhotos: [],
			itemid: ""
		};
	},

	componentWillMount: function(){
		if(!!this.props.prodImgaes){
			this.setState({prodPhotos: this.props.prodImgaes});
		}
	},

	componentDidMount: function(){
		$(function(){
			$(".prodPhotoImg").slick({
				dots: true
			});
		});
	},

	componentWillReceiveProps: function(nextProps){
		if(!!nextProps.prodImgaes){
			this.setState({prodPhotos: nextProps.prodImgaes});
		}

		$(function(){
			$(".prodPhotoImg").slick('unslick');
			$(".prodPhotoImg").slick({
				dots: true
			});
		});
	},

	render: function(){

		return(
			<div className="slider single-item prodPhotoImg">
				{
					(!!this.state.prodPhotos)
					?this.state.prodPhotos.map(function(imgUrl, index){
						return <div key={index}><img src={imgUrl} /></div>
					}, this)
					:null
				}
			</div>
		);
	}
});

module.exports = ProdPhotoView;



